body {
  box-sizing: border-box;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

article {
  margin: 0;
  padding: 0;
}

.people {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  align-items: center;
  gap: 40px 30px;
  padding: 30px 35px;
  display: grid;
}

@media (width >= 500px) {
  .people {
    --columns: 2;
  }
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

.person {
  text-align: center;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.photo {
  position: relative;
}

.person__photo {
  object-fit: cover;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin-bottom: 8px;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 84px;
  right: 20px;
}

.person__extra {
  display: none;
}

.person__name {
  color: #2f4f4f;
  margin: 0;
  font-size: 20px;
  font-weight: 700;
}
